.store {
    background-color: $bg-grey;
    padding-top: 30px;
    padding-bottom: 70px;
}

.store__grid {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    margin-top: 50px;
}

.store__title {
    font-size: 32px;
}

.store__top-wrapper {
    display: block;
    @media (min-width: $tablet) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.store__filter-btn {
    font-weight: bold;
    font-size: 16px;
    margin-top: 20px;
    width: 100%;

    @media (min-width: $tablet) {
        margin-top: 0;
        width: 200px;
    }

    @media (min-width: $desktop) {
        display: none;
    }
}

.store__breadcrumbs {
    margin-top: 15px;
}