.breadcrumbs__list {
    list-style: none;
    display: flex;
    font-size: 18px;
    font-weight: bold;
    flex-wrap: wrap;
}

.breadcrumbs__item {
    margin-right: 20px;
    margin-left: 20px;
    display: flex;
    align-items: center;
    line-height: 2;

    &:first-child {
        margin-left: 0;
    }

    &:nth-child(n+2) {
        position: relative;
        &::before {
            content: "";
            width: 7px;
            height: 7px;
            transform: rotate(45deg);
            display: block;
            position: absolute;
            background: $black;
            left: -23px;
        }
    }

    &:last-child {
        margin-right: 0;
    }
}


.breadcrumbs__link[href] {
    text-decoration: none;
    color: $black;
    &:hover {
        color: $brown-light;
    }
}

.breadcrumbs__link:not([href]) {
    color: $text-light;
}